import React, { useState } from 'react';
import axios from 'axios';

// setToken and setView allow this component to control the main App.js state
const Login = ({ setToken, setView }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // 1. Authenticate with the Django backend
      const response = await axios.post('http://127.0.0.1:8000/api/token/', { 
        username, 
        password 
      });
      
      // 2. Save tokens to browser memory (LocalStorage)
      localStorage.setItem('access_token', response.data.access);
      localStorage.setItem('refresh_token', response.data.refresh);
      
      // 3. FIX: Save the role specifically to clear the "UNDEFINED" error on the map
      localStorage.setItem('user_role', response.data.role); 
      
      // 4. Update the App state and enter the platform
      setToken(response.data.access);
      window.location.href = '/map';
      
    } catch (error) {
      console.error("Login error:", error);
      alert("Invalid credentials. Please try again.");
    }
  };

  return (
    <div style={containerStyle}>
      <form onSubmit={handleSubmit} style={formStyle}>
        <h2 style={{ textAlign: 'center', color: '#1a73e8', marginBottom: '25px' }}> Maritime Login</h2>
        
        <div style={{ marginBottom: '15px' }}>
          <label style={labelStyle}>Username</label>
          <input 
            type="text" 
            placeholder="Enter your username" 
            onChange={e => setUsername(e.target.value)} 
            style={inputStyle} 
            required 
          />
        </div>

        <div style={{ marginBottom: '10px' }}>
          <label style={labelStyle}>Password</label>
          <input 
            type="password" 
            placeholder="Enter your password" 
            onChange={e => setPassword(e.target.value)} 
            style={inputStyle} 
            required 
          />
        </div>

        {/* --- Product Feature: Forgot Password --- */}
        <div style={{ textAlign: 'right', marginBottom: '20px' }}>
          <span 
            onClick={() => alert("Verification link sent to your registered email.")} 
            style={{ color: '#1a73e8', fontSize: '0.85rem', cursor: 'pointer', textDecoration: 'underline' }}
          >
            Forgot Password?
          </span>
        </div>

        <button type="submit" style={buttonStyle}>
          Login to Dashboard
        </button>

        {/* --- Product Feature: Create Account --- */}
        <p style={{ textAlign: 'center', marginTop: '15px', fontSize: '0.9rem', color: '#64748b' }}>
          New to the platform? 
          <span 
            onClick={() => setView('register')} 
            style={{ color: '#1a73e8', cursor: 'pointer', marginLeft: '5px', fontWeight: 'bold' }}
          >
            Create an account
          </span>
        </p>
      </form>
    </div>
  );
};

// --- Professional UI Styles ---
const containerStyle = { display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh', background: '#f0f2f5' };
const formStyle = { background: '#fff', padding: '40px', borderRadius: '15px', boxShadow: '0 4px 15px rgba(0,0,0,0.1)', width: '380px' };
const labelStyle = { display: 'block', marginBottom: '5px', fontSize: '0.9rem', color: '#334155', fontWeight: '500' };
const inputStyle = { width: '100%', padding: '12px', borderRadius: '8px', border: '1px solid #cbd5e1', boxSizing: 'border-box', outline: 'none' };
const buttonStyle = { width: '100%', padding: '12px', background: '#1a73e8', color: '#fff', border: 'none', borderRadius: '8px', cursor: 'pointer', fontWeight: 'bold', fontSize: '1rem' };

export default Login;